body, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 18px;
}

.container {
  box-sizing: border-box;
  max-width: 1300px;
  margin: 0 auto;
  display: grid;
}

.header {
  box-shadow: 0 2px 4px #0000000d;
}

.header__box {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  height: 40px;
}

.nav__menu {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition-duration: .3s;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__item {
  height: 60px;
  line-height: 60px;
  position: relative;
}

.nav:last-child {
  margin-right: 0;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__img {
  justify-content: center;
  align-items: center;
  display: flex;
}

.card__img img {
  width: 160px;
  height: 134px;
}

.card__id {
  color: #616070;
  margin-bottom: 16px;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card .review__points {
  line-height: 14px;
}

.card .stars {
  margin-right: 4px;
  display: flex;
}

.card .stars--0, .card .stars--1, .card .stars--2, .card .stars--3, .card .stars--5 {
  display: none;
}

.card .stars--1 :nth-child(-n+1), .card .stars--2 :nth-child(-n+2), .card .stars--3 :nth-child(-n+3), .card .stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card .stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.card .stars__star:not(:last-child) {
  margin-right: 4px;
}

.card__price-block {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
}

.card__price-current {
  font-size: 16px;
  font-weight: 700;
}

.card__price-current:before {
  content: "$";
}

.card__btn {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: .3s;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__review-block {
  justify-content: space-between;
  align-items: end;
  display: flex;
}

.card .reviews {
  color: #060b35;
  line-height: 14px;
}
